/* new */

body {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  background-color: #1a1a1a;
  color: #fff;
}

.App {
  text-align: center;
  color: #fff;
}

.App-header {
  padding: 50px 20px;
  background: #000;
}

.App-header h1 {
  font-size: 3rem;
  margin: 0;
}

.App-header p {
  font-size: 1.2rem;
  color: #ccc;
}

section {
  padding: 40px 20px;
}

.services, .work {
  max-width: 1200px;
  margin: 0 auto;
}

.services h2, .work h2 {
  font-size: 2.5rem;
  margin-bottom: 30px;
}

.service-list, .work-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.service-item, .work-item {
  background: #333;
  margin: 10px;
  padding: 20px;
  border-radius: 5px;
  width: 250px;
  text-align: left;
  transition: transform 0.3s ease;
}

.service-item:hover, .work-item:hover {
  transform: translateY(-10px);
}

.service-item h3, .work-item h3 {
  font-size: 1.8rem;
  margin-bottom: 10px;
}

.service-item p, .work-item p {
  font-size: 1rem;
  color: #bbb;
}

.work-item img {
  width: 100%;
  height: auto;
  margin-bottom: 10px;
  border-radius: 5px;
}
